import { Square } from "./square";
import { mat4 } from "gl-matrix";
import { object_types } from "./definitions";
export class Scene {
    constructor() {
        this.squares = [];
        this.object_data = new Float32Array(16 * 1024);
        this.square_count = 0;
        this.quad_count = 0;
        this.make_squares();
        //this.make_quads();
    }
    make_squares() {
        var i = 0;
        for (var y = -5; y <= 5; y++) {
            this.squares.push(new Square([2, y, 0], 0));
            var blank_matrix = mat4.create();
            for (var j = 0; j < 16; j++) {
                this.object_data[16 * i + j] = blank_matrix.at(j);
            }
            i++;
            this.square_count++;
        }
    }
    update() {
        var i = 0;
        this.squares.forEach((square) => {
            var model = square.get_model();
            for (var j = 0; j < 16; j++) {
                this.object_data[16 * i + j] = model.at(j);
            }
            i++;
        });
    }
    get_renderables() {
        return {
            //check
            view_transform: mat4.lookAt(mat4.create(), [0, 0, 10], [0, 0, 0], [0, 1, 0]),
            model_transforms: this.object_data,
            object_counts: {
                [object_types.TRIANGLE]: this.square_count,
            }
        };
    }
}
